
            @use 'sass:math';
            @use "src/styles/variables" as *;
            @use "src/styles/mixins" as *;
          
.chapter {
  min-height: 100%;
  &__container {
    min-height: 100%;
  }

  &__header {
    margin-bottom: 30px;
    @media (max-width: $tablet) {
      margin-bottom: 25px;
    }
    @media (max-width: $mobile) {
      margin-bottom: 20px;
    }
    @media (max-width: $mobileSmall) {
      margin-bottom: 15px;
    }
  }

  &__sub-title {
    font-family: $fontFamilyTitle;
    font-size: 24px;
    line-height: 1.3;
    font-weight: 500;
    margin-bottom: 20px;
    @media (max-width: $tablet) {
      font-size: 20px;
    }
    @media (max-width: $mobile) {
      font-size: 18px;
      margin-bottom: 17px;
    }
    @media (max-width: $mobileSmall) {
      font-size: 16px;
      margin-bottom: 13px;
    }
  }

  &__body {
    min-height: 0;
    height: 100%;
  }

  &__slider {
    min-height: 0;
    height: 100%;
  }

  &__wrapper {
    min-height: 0;
    height: 100%;
    align-items: stretch;
  }

  &__slide {
    min-height: 0;
    height: 100%;
    width: auto;
  }

  &__button {
    position: absolute;
    top: 0;
    z-index: 3;
    width: 70px;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 10px;
    transition: opacity 0.5s ease 0s, visibility 0.5s ease 0s;

    &--prev {
      left: 0;
      justify-content: flex-start;
      background: linear-gradient(
        -90deg,
        rgba(var(--bg-color-rgb), 0),
        rgba(var(--bg-color-rgb), 0.5) 28.365%,
        rgb(var(--bg-color-rgb)) 100%
      );
      img {
        transform: rotate(180deg);
      }
    }
    &--next {
      justify-content: flex-end;
      background: linear-gradient(
        90deg,
        rgba(var(--bg-color-rgb), 0),
        rgba(var(--bg-color-rgb), 0.5) 28.365%,
        rgb(var(--bg-color-rgb)) 100%
      );
      right: 0;
    }

    &.swiper-button-disabled {
      opacity: 0;
      visibility: hidden;
    }

    @media (max-width: $mobile) {
      display: none;
    }
  }
}
