._bg {
  width: 100%;
  height: 100%;
  &--cover {
    object-fit: cover;
  }
  &--contain {
    object-fit: contain;
  }
}

.image-centered {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--bg-color-light);
  overflow: hidden;
  img {
    width: 100%;
  }
  &--height {
    img {
      height: 100%;
      width: auto;
    }
  }
}

.visible-line {
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;

  &--2 {
    -webkit-line-clamp: 2;
  }
  &--3 {
    -webkit-line-clamp: 3;
  }
  &--4 {
    -webkit-line-clamp: 4;
  }
  &--5 {
    -webkit-line-clamp: 5;
  }
}



.border {
  border: 1px solid var(--main-color);
  border-radius: 10px;

  @at-root a#{&} {
    @media (any-hover: hover) {
      transition: border-color 0.3s ease 0s;
      &:hover {
        border-color: var(--primary-color-dark);
      }
    }
  }
}

.border-opacity {
  border: 1px solid rgba(var(--main-color-rgb), 0.3);
  border-radius: 10px;

  @at-root a#{&} {
    @media (any-hover: hover) {
      transition: border-color 0.3s ease 0s;
      &:hover {
        border-color: rgba(var(--main-color-rgb), 0.5);
      }
    }
  }
}

.border-radius {
  border-radius: 10px;
}

.tab-grid-3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 15px;

  @include media-max($mobile) {
    grid-template-columns: repeat(2, 1fr);
  }
  @include media-max($mobileSmall) {
    grid-template-columns: 1fr;
    gap: 12px;
  }
}

.tab-grid-2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 15px;

  @include media-max(600px) {
    grid-template-columns: 1fr;
  }
  @include media-max($mobileSmall) {
    gap: 12px;
  }
}

.status {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 12px;
  font-weight: 600;
  background-color: var(--primary-color);
  border-radius: 2px;
  padding: 3px;

  &._released {
    background-color: var(--released-color);
  }
  &._ongoing {
    background-color: var(--ongoing-color);
  }
  &._anons {
    background-color: var(--anons-color);
  }
  &._unknown {
    background-color: var(--anons-unknown);
  }

  &--shadow {
    box-shadow: 0px 0px 5px 2px var(--main-color);
  }
}

.score {
  display: inline-flex;
  align-items: center;
  font-size: 14px;
  font-weight: 600;
  border-radius: 5px;
  background-color: var(--primary-color);
  padding: 4px 5px 4px 2px;
  &::before {
    content: '';
    display: inline-block;
    width: 12px;
    height: 12px;
    background: url('/assets/star.svg') center / contain no-repeat;
    margin-right: 3px;
    margin-top: -1px;
  }

  &--shadow {
    box-shadow: 0px 0px 4px 2px rgb(255, 255, 255);
  }
}

.bnts-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.btn {
  $btn: &;
  font-weight: 500;
  text-align: center;
  padding: 15px 25px;
  border-radius: 10px;
  background-color: var(--primary-color);
  transition: background-color 0.3s ease, color 0.3s ease;

  @include media-max($mobile) {
    font-size: 14px;
  }

  &--upper {
    font-weight: 600;
    text-transform: uppercase;
  }

  @media (any-hover: hover) {
    &:hover {
      background-color: var(--primary-color-dark);
    }
  }

  &--transparent,
  &--outline {
    background-color: transparent;

    @media (any-hover: hover) {
      &:hover {
        background-color: var(--main-color);
        color: var(--bg-color);
      }
    }
  }

  &--outline {
    border: 1px solid var(--main-color);
  }

  &--white {
    background-color: var(--main-color);
    color: var(--bg-color);

    @media (any-hover: hover) {
      &:hover {
        // background-color: var(--primary-color);
        color: var(--main-color);
      }
    }
  }

  &--icon {
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 7px;
    svg,
    img {
      flex-shrink: 0;
      width: 20px;
      height: 20px;
      object-fit: contain;
    }
  }

  &--fill {
    svg path {
      fill: var(--main-color);
      transition: fill 0.3s ease;
    }

    &#{$btn}--white {
      svg path {
        fill: var(--bg-color);
      }

      @media (any-hover: hover) {
        &:hover {
          svg path {
            fill: var(--main-color);
          }
        }
      }
    }

    &#{$btn}--transparent {
      @media (any-hover: hover) {
        &:hover {
          svg path {
            fill: var(--bg-color);
          }
        }
      }
    }
  }

  &--stroke {
    svg path {
      stroke: var(--main-color);
      transition: stroke 0.3s ease;
    }

    &#{$btn}--white {
      svg path {
        stroke: var(--bg-color);
      }

      @media (any-hover: hover) {
        &:hover {
          svg path {
            stroke: var(--main-color);
          }
        }
      }
    }

    &#{$btn}--transparent {
      @media (any-hover: hover) {
        &:hover {
          svg path {
            stroke: var(--bg-color);
          }
        }
      }
    }
  }
}

.show-more-btn {
  width: 100%;
  @include media-min($mobileSmall) {
    max-width: 320px;
  }
}

