.title {
  font-family: 'Inknut Antiqua', serif;
  color: var(--primary-color);
}

.link {
  position: relative;
  padding: 0 1px;
  &::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);
    width: 100%;
    height: 100%;
    pointer-events: none;

    border: 1px dashed transparent;
    border-bottom-color: #fff;
    transition: border-color 0.15s ease 0s;
  }
  @media (any-hover: hover) {
    &:hover::before {
      border-color: #fff;
    }
  }

  &--primary {
    color: var(--primary-color);
    font-weight: 500;
    &::before {
      border-bottom-color: var(--primary-color);
    }
    @media (any-hover: hover) {
      &:hover::before {
        border-color: var(--primary-color);
      }
    }
  }
}

.info-row {
  display: flex;
  flex-wrap: wrap;
  row-gap: 2px;

  font-size: 14px;
  line-height: math.div(16, 12);

  &__name {
    opacity: 0.75;
  }

  &__values {
    display: flex;
    flex-wrap: wrap;
    gap: 5px 10px;
    margin-left: 15px;
    font-weight: 500;
  }

  &__value {
    span {
      opacity: 0.5;
    }

    &._capitalize {
      text-transform: capitalize;
    }
  }
}

//========================================================================================================================================================
.loading {
  font-family: var(--font-family-title);
  text-align: center;
  &__animation {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
    svg {
      width: 32px;
      height: 32px;
      object-fit: contain;
      animation: loading 2.5s ease-in-out infinite;
      animation-delay: -2.5s;

      &:nth-child(1) {
        animation-delay: -2s;
      }
      &:nth-child(2) {
        animation-delay: -1.75s;
      }
      &:nth-child(3) {
        animation-delay: -1.5s;
      }
      &:nth-child(4) {
        animation-delay: -1.25s;
      }
      &:nth-child(5) {
        animation-delay: -1s;
      }
    }
  }
  &__text {
    span {
      animation: loading 2.5s ease-in-out infinite;
      animation-delay: -2.5s;

      &:nth-child(1) {
        animation-delay: -2s;
      }
      &:nth-child(2) {
        animation-delay: -1.75s;
      }
      &:nth-child(3) {
        animation-delay: -1.5s;
      }
    }
  }
}

@keyframes loading {
  0% {
    opacity: 1;
  }
  40% {
    opacity: 0;
  }
  60% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.empty-value-message {
  display: flex;
  align-items: flex-end;
  &__image {
    flex-shrink: 0;
    align-self: flex-start;
    width: 37px; // 33px;
    height: 59px; //51px;
    object-fit: contain;
    margin-right: 10px;
    @include media-max($mobile) {
      width: 28px;
      height: 43px;
    }
  }

  &__text {
    line-height: 1.3;
    @include media-max($mobile) {
      font-size: 14px;
    }
  }
}
