
            @use 'sass:math';
            @use "src/styles/variables" as *;
            @use "src/styles/mixins" as *;
          
.anime-videos {
  &__section {
    & + & {
      margin-top: 35px;
    }
  }

  &__section-header {
    margin-bottom: 20px;
  }

  &__items {
    &:not(:last-child) {
      margin-bottom: 20px;
      @include media-max($mobile) {
        margin-bottom: 15px;
      }
    }
  }

  &__item {
  }
}
.video-item {
  aspect-ratio: 16/9;
  // aspect-ratio: 3/2;
  position: relative;
  border-radius: 10px;
  overflow: hidden;

  &__title,
  &__play-btn {
    box-shadow: 0px 0px 5px 2px var(--main-color);
    background-color: rgba(var(--bg-color-rgb), 0.75);
  }

  &__title {
    font-size: 12px;
    line-height: 1.4;
    font-weight: 500;
    display: inline-block;
    padding: 2px 5px;
    margin: 7px;
    border-radius: 5px;
    position: relative;
    z-index: 2;
  }

  &__image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    &._not-found {
      object-position: top;
    }
  }

  &__play-btn {
    font-family: var(--font-family-title);
    font-size: 14px;
    font-weight: 600;
    line-height: math.div(20, 16);
    display: inline-flex;
    align-items: center;
    justify-content: center;
    gap: 6px;
    padding: 8px 14px;
    border: 1px solid var(--main-color);
    border-radius: 8px;
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 3;
    transform: translate3d(-50%, -50%, 0);
  }
}

