
            @use 'sass:math';
            @use "src/styles/variables" as *;
            @use "src/styles/mixins" as *;
          
.random-anime {
  &__inner {
    display: flex;
    min-height: 450px;
  }

  &__image {
    flex-basis: 560px;
    width: 560px;
    min-width: 320px;
    position: relative;
    img,
    .skeleton__img {
      position: absolute;
      top: 0;
      right: 0;
      width: calc(#{$containerPadding}px / 2 + 100%);
      height: 100%;
      object-fit: cover;
      border-radius: 0 20px 20px 0;
      @include media-min($pc) {
        width: calc(50vw - #{$maxWidthContainer}px / 2 + 100%);
      }
    }
  }

  &__body {
    flex: 1 1 auto;
    padding: 50px 0 50px 50px;
    @include media-max($tablet) {
      padding-left: 35px;
    }
  }

  &__content {
    max-width: 720px;
    width: 100%;
  }

  &__section-title {
    display: inline-block;
    font-size: 36px;
    font-weight: 500;
    line-height: 1.2;
    color: var(--main-color);
    padding: 17px 25px;
    border-radius: 10px;
    margin-bottom: 32px;
    transform: translate3d(-300px, 0, 0);
    background: linear-gradient(
      90deg,
      rgb(var(--bg-color-rgb)) 0%,
      rgba(var(--bg-color-rgb), 0.5) 69.333%,
      rgba(var(--bg-color-rgb), 0) 100%
    );

    @media (max-width: $tablet) {
      font-size: 32px;
      transform: translate3d(-275px, 0, 0);
    }
    @media (max-width: $mobile) {
      font-size: 26px;
    }
    @media (max-width: $mobileSmall) {
      font-size: 20px;
    }
  }

  &__title {
    font-size: 48px;
    font-weight: 700;
    line-height: 1.2;
    margin-bottom: 30px;

    @include media-max($tablet) {
      font-size: 36px;
    }
    @include media-max($mobile) {
      font-size: 28px;
    }
    @include media-max($mobileSmall) {
      font-size: 24px;
    }
  }

  &__info {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 12px 24px;
    &:not(:last-child) {
      margin-bottom: 25px;
    }
  }

  &__score {
    font-size: 18px;
    padding: 6px 7px 6px 4px;
    &::before {
      width: 16px;
      height: 16px;
      @include media-max($tablet) {
        width: 15px;
        height: 15px;
      }
      @include media-max($mobile) {
      }
    }

    @include media-max($tablet) {
      font-size: 16px;
      padding: 5px 6px 5px 5px;
    }
  }

  &__link,
  &__info-text {
    font-size: 18px;
    line-height: 1.2;
    font-weight: 500;
    @include media-max($tablet) {
      font-size: 14px;
    }
    @include media-max($mobileSmall) {
      font-size: 12px;
    }
  }

  &__info-text {
  }

  &__text {
    font-size: 20px;
    font-weight: 500;
    line-height: math.div(32, 20);
    &:not(:last-child) {
      margin-bottom: 25px;
    }

    @include media-max($tablet) {
      font-size: 16px;
    }
    @include media-max($mobile) {
      font-size: 14px;
    }
  }

  &__genres {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 12px 24px;
  }
}
