
            @use 'sass:math';
            @use "src/styles/variables" as *;
            @use "src/styles/mixins" as *;
          
.anime {
  &__intro {
    @include media-max($tablet) {
      // margin-bottom: 25px;
    }
    @include media-max($mobile) {
      // margin-bottom: 15px;
    }
  }
  &__about {
  }

  & .section-header {
    // border-bottom: 1px solid var(--secondary-color);
    row-gap: 10px;
    & .section-header__title {
      // font-family: $fontFamily;
      font-size: 20px;
      font-weight: 600;
      line-height: math.div(22, 18);
      // border: none;
      // padding-left: 6px;
      padding-left: 8px;
      letter-spacing: 1px;
      // border-left: 5px solid var(--secondary-color);
      border-left-width: 6px;

      @include media-max($mobile) {
        font-size: 18px;
      }
      @include media-max($mobileSmall) {
        font-size: 17px;
        border-left-width: 5px;
        padding-left: 7px;
      }
    }
    & .section-header__link {
      font-size: 18px;
      font-weight: 400;
      line-height: math.div(22, 18);

      @include media-max($mobile) {
        font-size: 16px;
      }
      @include media-max($mobileSmall) {
        font-size: 14px;
      }
    }
  }
}

.anime-intro {
  position: relative;
  display: flex;
  align-items: flex-end;
  min-height: 360px;
  padding: 100px 0 20px;
  border-radius: 20px;
  overflow: hidden;

  img {
    position: absolute;
    top: 0;
    left: 0;
  }

  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    background: #000000;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.85) 0%, rgba(0, 0, 0, 0.5) 100%);
  }

  &__bg {
    &._not-found {
      object-position: top;
    }
  }

  &__container {
    position: relative;
    z-index: 2;
  }

  &__body {
    // margin-left: 376px;
    margin-left: 27.446%;
    @include media-max($mobile) {
      margin-left: unset;
      text-align: center;
    }
  }

  &__title {
    font-size: 42px;
    font-weight: 700;
    line-height: 1.2;
    color: var(--primary-color);
    &:not(:last-child) {
      margin-bottom: 15px;
      @include media-max($tablet) {
        margin-bottom: 12px;
      }
    }

    @include media-max($tablet) {
      font-size: 36px;
    }
    @include media-max($mobile) {
      font-size: 28px;
    }
    @include media-max($mobileSmall) {
      font-size: 24px;
    }
  }

  &__sub-title {
    font-size: 18px;
    font-weight: 500;
    line-height: 1.2;
    color: rgba(255, 255, 255, 0.75);

    & + & {
      margin-top: 10px;
    }

    @include media-max($tablet) {
      font-size: 16px;
    }
    @include media-max($mobileSmall) {
      font-size: 14px;
    }
  }
}

//========================================================================================================================================================
.anime-about {
  position: relative;
  z-index: 3;

  &__inner {
    display: flex;
    column-gap: 30px;
    padding-top: 20px;
    @include media-max($pc) {
      // column-gap: 2.14285714vw;
      column-gap: 25px;
    }
    @include media-max($tablet) {
      display: block;
    }
  }

  &__left {
    @include media-max($tablet) {
      margin-bottom: 25px;
    }
  }

  &__body {
    display: flex;
    flex-direction: column;
    width: 100%;
    min-width: 0;
    @include media-max($tablet) {
      margin-bottom: 20px;
    }
    @include media-max($mobileSmall) {
      margin-bottom: 15px;
    }
  }

  &__breadcrumbs {
    margin-bottom: 15px;
  }

  &__tabs {
    background-color: var(--bg-color-light);
    border-radius: 10px;
    overflow: hidden;
    height: 100%;
  }
}
.anime-leftside {
  width: 100%;
  max-width: 25.256%;
  margin-top: -165px;
  @include media-max($pc) {
    max-width: 23%;
  }
  @include media-max($tablet) {
    max-width: 100%;
    display: flex;
    margin-top: 0;
    position: relative;
    padding-left: 27.446%;
  }
  @include media-max($mobile) {
    padding-left: unset;
  }

  &__poster {
    height: 489px;
    border-radius: 10px;
    overflow: hidden;
    position: relative;
    background-color: var(--bg-color-light);

    &:not(:last-child) {
      margin-bottom: 17px;
      @include media-max($tablet) {
        margin-bottom: 0;
      }
      @include media-max($mobile) {
        margin-bottom: 25px;
      }
    }

    img {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 100%;
      transform: translate3d(-50%, -50%, 0);
    }

    @include media-max($pc) {
      // height: 34.9285714vw;
      height: 32vw;
    }
    @include media-max($tablet) {
      width: 25.256%;
      position: absolute;
      bottom: 0;
      left: 0;
    }
    @include media-max($mobile) {
      position: relative;
      min-width: 200px;
      min-height: 280px;
      margin: 0 auto;
    }
  }

  &__buttons {
    display: flex;
    flex-direction: column;
    &:not(:last-child) {
      margin-bottom: 17px;
    }
    @include media-max($tablet) {
      width: 100%;
    }

    & > * + * {
      margin-top: 17px;
      @include media-max($mobile) {
        margin-top: 12px;
      }
    }
  }

  &__btn {
    padding: 10px 15px;

    svg {
      width: 20px;
      height: 20px;
    }
  }

  &__resources {
    background-color: var(--bg-color-light);
    border-radius: 10px;
    padding: 15px 15px 18px;
    @include media-max($mobile) {
      padding: 10px 10px 13px;
    }
  }

  &__item {
    & + & {
      margin-top: 22px;
    }
  }

  &__title {
    font-weight: 600;
    line-height: math.div(20, 16);
    text-transform: capitalize;
    padding-bottom: 1px;
    border-bottom: 1px solid rgba(var(--main-color-rgb, 0.7));
  }

  &__list {
  }
}

.leftside-list {
  padding-top: 10px;

  &__item {
    display: flex;

    &::before {
      content: '';
      width: 15px;
      height: 15px;
      background: url('/assets/link.svg') center / contain no-repeat;
      margin-right: 7px;
      margin-top: 1px;
    }

    & + & {
      margin-top: 8px;
      @include media-max($tablet) {
        margin-top: 12px;
      }
    }
  }

  &__link {
    align-self: center;
    font-size: 14px;
    text-decoration-line: underline;
    text-underline-offset: 1px;
    @media (any-hover: hover) {
      transition: color 0.3s ease 0s;
      &:hover {
        color: var(--primary-color);
      }
    }
  }
}

.breadcrumbs {
  display: flex;
  &__item {
    &:not(:last-child) {
      margin-right: 8px;
      &::after {
        content: '>';
        margin-left: 8px;
      }
    }
  }

  &__link,
  &__current {
    font-size: 16px;
    line-height: math.div(20, 16);
  }

  &__current {
    font-weight: 500;
    color: var(--primary-color);
  }
}

.anime-tabs {
  display: flex;
  flex-direction: column;

  &__list {
    max-width: 100%;
    display: flex;
    gap: 1px;
    // margin-bottom: 5px;
    // border-radius: 10px;
    background-color: var(--bg-color-light);
    // margin-right: auto;
    padding: 3px;
    border-bottom: 2px solid var(--bg-color);

    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    // scrollbar-width: none; // Firefox
    &::-webkit-scrollbar {
      // display: none;
    }
  }

  &__trigger {
    // min-width: 120px;
    flex-shrink: 0;
    white-space: nowrap;
    // padding: 4px 10px;
    padding: 5px 25px;
    border-radius: 7px;
    background-color: var(--bg-color-light);
    transition-duration: 0.3s;
    transition-timing-function: ease;
    transition-property: font-weight, color, background-color;

    &[aria-selected='true'] {
      font-weight: 600;
      background-color: var(--primary-color);
      color: var(--main-color);
    }

    @media (any-hover: hover) {
      &:hover {
        background-color: var(--primary-color-dark);
      }
    }
  }

  &__body {
    flex: 1 1 auto;
    padding: 20px 15px;
    border-radius: 10px;
    background-color: var(--bg-color-light);
    @include media-max($mobile) {
      padding: 15px;
    }
  }

  &__content {
    & > * + * {
      margin-top: 40px;
    }
  }
}

.anime-video-trailer .lg-outer {
  padding-bottom: 50px;
}

