
            @use 'sass:math';
            @use "src/styles/variables" as *;
            @use "src/styles/mixins" as *;
          
.card-item {
  $self: &;
  width: 220px;
  overflow: hidden;

  @media (max-width: $mobile) {
    width: 200px;
  }

  &__image {
    position: relative;
    width: 100%;
    height: 300px;
    border-radius: inherit;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--bg-color-light);

    @media (max-width: $mobile) {
      height: 280px;
    }

    img {
      width: 100%;
      transition: transform 0.3s ease 0s;
      #{$self}:hover & {
        transform: scale(1.02);
      }
    }
  }

  &__status,
  &__score {
    position: absolute;
    z-index: 1;
  }

  &__status {
    top: 10px;
    left: 10px;
  }

  &__score {
    bottom: 10px;
    right: 10px;
  }

  &__content {
    padding: 8px 8px 5px;
  }

  &__title {
    font-size: 16px;
    font-weight: 700;
    line-height: math.div(20, 16);
    margin-bottom: 5px;
    text-decoration: underline;
    text-decoration-color: transparent;
    transition: color 0.3s ease 0s, text-decoration-color 0.3s ease 0s;

    #{$self}:hover & {
      color: var(--primary-color-dark);
      text-decoration-color: var(--primary-color-dark);
    }
  }

  &__text {
    font-size: 14px;
    font-weight: 500;
    line-height: math.div(18, 14);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 5px 10px;
  }
}
