
            @use 'sass:math';
            @use "src/styles/variables" as *;
            @use "src/styles/mixins" as *;
          
.anime-news {
  &__items {
   &:not(:last-child) {
      margin-bottom: 20px;
      @include media-max($mobile) {
        margin-bottom: 15px;
      }
    }
  }
  &__item {
    & + & {
      margin-top: 15px;
      @include media-max($mobileSmall) {
        margin-top: 12px;
      }
    }
  }
}
.news-item {
  border: 1px solid var(--main-color);
  border-radius: 10px;
  padding: 10px;

  &__title {
    font-size: 16px;
    font-weight: 600;
    line-height: math.div(24, 18);
    margin-bottom: 5px;
    @include media-max($mobileSmall) {
      font-size: 14px;
    }
  }

  &__info {
    font-size: 12px;
    line-height: 16px;
    opacity: 0.7;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 7px 25px;
    margin-bottom: 12px;
  }

  &__date {
  }

  &__author {
  }

  &__text {
    font-size: 14px;
    line-height: math.div(20, 16);
    opacity: 0.7;
    @include media-max($mobileSmall) {
      font-size: 13px;
    }
  }
}
