
            @use 'sass:math';
            @use "src/styles/variables" as *;
            @use "src/styles/mixins" as *;
          
.section-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  flex-wrap: wrap;
  gap: 20px 50px;
  @media (max-width: $tablet) {
    row-gap: 15px;
  }
  @media (max-width: $mobile) {
    row-gap: 10px;
  }
  @media (max-width: $mobileSmall) {
    row-gap: 5px;
  }

  &__title {
    color: #fff;
    font-size: 36px;
    line-height: math.div(48, 36);
    font-weight: 700;
    padding-left: 15px;
    border-left: 10px solid var(--primary-color);
    @media (max-width: $tablet) {
      font-size: 32px;
      border-width: 8px;
    }
    @media (max-width: $mobile) {
      font-size: 26px;
      border-width: 6px;
      padding-left: 10px;
    }
    @media (max-width: $mobileSmall) {
      font-size: 20px;
      border-width: 4px;
      padding-left: 7px;
    }
  }

  &__link {
    color: var(--primary-color);
    font-size: 20px;
    line-height: math.div(29, 24);
    line-height: 29px;
    text-transform: capitalize;
    display: flex;
    align-items: center;
    svg {
      display: inline-block;
      width: 25px;
      height: 12px;
      background: url('/assets/long-arrow.svg') center / contain no-repeat;
      margin-left: 6px;
      @media (max-width: $mobile) {
        width: 20px;
        height: 10px;
      }
      @media (max-width: $mobileSmall) {
        width: 18px;
        height: 9px;
      }
    }

    @media (any-hover: hover) {
      transition: color 0.3s ease 0s;
      svg {
        transition: transform 0.3s ease 0s;
        path {
          transition: fill 0.3s ease 0s;
        }
      }

      &:hover {
        color: var(--primary-color-dark);
        svg {
          transform: translate3d(3px, 0, 0);
          path {
            fill: var(--primary-color-dark);
          }
        }
      }
    }

    @media (max-width: $tablet) {
      font-size: 18px;
    }
    @media (max-width: $mobile) {
      font-size: 16px;
    }
    @media (max-width: $mobileSmall) {
      font-size: 14px;
    }
  }
}
