
            @use 'sass:math';
            @use "src/styles/variables" as *;
            @use "src/styles/mixins" as *;
          
.intro {
  min-height: 100vh;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  &__slider {
    position: relative;
    :global {
      .swiper-pagination {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        position: absolute;
        bottom: 17px;
        width: 100%;
        z-index: 2;

        &.swiper-pagination-lock {
          display: none;
        }

        .swiper-pagination-bullet {
          flex-shrink: 0;
          width: 27px;
          height: 4px;
          background-color: #fff;
          transition: width 0.3s ease 0s;
          border-radius: 0;
          opacity: 1;
          margin: 5px;
          cursor: pointer;

          @include media-max($tablet) {
            width: 20px;
            height: 3px;
            margin: 3px;
          }
        }
        .swiper-pagination-bullet-active {
          width: 50px;
          background-color: var(--primary-color);
          @include media-max($tablet) {
            width: 38px;
          }
        }
      }
    }
  }
  &__wrapper {
  }
  &__slide {
    min-height: 100vh;
  }
}

.slide {
  padding: 50px 0;
  height: 100%;
  position: relative;
  display: flex;
  align-items: flex-end;
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(
      270deg,
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 0.7) 30%,
      rgb(0, 0, 0) 70%
    );
    @include media-max($pc) {
      background: linear-gradient(
        270deg,
        rgba(0, 0, 0, 0.3) -10%,
        rgba(0, 0, 0, 0.7) 30%,
        rgb(0, 0, 0) 70%
      );
    }

    @include media-max($mobile) {
      background: linear-gradient(
        270deg,
        rgba(0, 0, 0, 0.3) -10%,
        rgba(0, 0, 0, 0.7) 30%,
        rgb(0, 0, 0) 90%
      );
    }
  }

  &__container {
    display: flex;
    align-items: flex-end;
  }
  &__bg {
    position: absolute;
    top: 0;
    // left: calc(50% - 1440px / 4);
    // left: 29.915%;
    // width: 100%;
    right: 0;
    width: 70.085%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    @include media-max($mobile) {
      width: 90.085%;
    }

    &--not-found {
      object-position: top;
    }
  }

  &__body {
    position: relative;
    z-index: 2;
    display: flex;
    align-items: flex-start;

    @include media-max($mobile) {
      display: block;
    }
  }

  &__image {
    flex-shrink: 0;
    width: 347px;
    height: 490px;
    margin-right: 30px;
    border-radius: 20px;
    overflow: hidden;

    opacity: 0;
    transform: translate3d(-100%, 0, 0);
    transition: transform 0.5s ease 0.05s, opacity 0.5s ease 0.05s;

    @include media-max($tablet) {
      width: 280px;
      height: 400px;
    }
    @include media-max($mobile) {
      display: none;
      // width: 250px;
      // height: 360px;
    }
  }

  &__content {
    align-self: flex-end;
    padding: 25px 0;
  }

  &__title {
    max-width: 800px;
    font-size: 48px;
    font-weight: 700;
    line-height: 124px;
    line-height: 1.2;
    &:not(:last-child) {
      margin-bottom: 23px;
      @include media-max($tablet) {
        margin-bottom: 18px;
      }
      @include media-max($mobile) {
        margin-bottom: 15px;
      }
      @include media-max($mobileSmall) {
        margin-bottom: 12px;
      }
    }

    opacity: 0;
    transform: translate3d(300px, 0, 0);
    transition: transform 0.5s ease 0s, opacity 0.5s ease 0s;

    @include media-max($tablet) {
      font-size: 36px;
    }
    @include media-max($mobile) {
      font-size: 28px;
    }
    @include media-max($mobileSmall) {
      font-size: 24px;
    }
  }

  &__text {
    max-width: 620px;
    font-size: 20px;
    line-height: 1.6;
    &:not(:last-child) {
      margin-bottom: 20px;
    }

    opacity: 0;
    transform: translate3d(280px, 0, 0);
    transition: transform 0.5s ease 0.05s, opacity 0.5s ease 0.05s;

    @include media-max($tablet) {
      font-size: 16px;
    }
    @include media-max($mobile) {
      font-size: 14px;
    }
    @include media-max($mobileSmall) {
      -webkit-line-clamp: 4;
    }
  }

  &__info {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 20px 40px;

    opacity: 0;
    transform: translate3d(280px, 0, 0);
    transition: transform 0.5s ease 0.1s, opacity 0.5s ease 0.1s;

    @include media-max($mobile) {
      row-gap: 15px;
    }
  }

  &__score-wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 10px;
  }

  &__score {
    display: inline-flex;
    align-items: center;
    font-size: 20px;
    font-weight: 600;
    color: var(--primary-color);
    line-height: 1;
    svg {
      height: 18px;
      width: 18px;
      margin-right: 5px;
      path {
        fill: var(--primary-color);
      }
    }

    @include media-max($tablet) {
      font-size: 18px;
      svg {
        width: 16px;
        height: 16px;
      }
    }
    @include media-max($mobile) {
      svg {
        position: relative;
        top: -1px;
      }
    }
    @include media-max($mobileSmall) {
      font-size: 16px;
    }
  }

  &__score-users {
    font-size: 12px;
    line-height: 1.2;
    opacity: 0.7;
    @include media-max($mobileSmall) {
      font-size: 10px;
    }
  }

  &__details {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 10px 20px;
    @include media-max($mobileSmall) {
      gap: 8px 15px;
    }
  }

  &__link {
    position: relative;
    border: 1px dashed transparent;
    border-bottom-color: #fff;
    padding: 2px;
    transition: border-color 0.3s ease 0s;
    @media (any-hover: hover) {
      &:hover {
        border-color: #fff;
      }
    }

    &--rating {
      color: var(--primary-color);
      font-weight: 500;
      border-bottom-color: var(--primary-color);
      @media (any-hover: hover) {
        &:hover {
          border-color: var(--primary-color);
        }
      }
    }

    @include media-max($tablet) {
      font-size: 14px;
    }
    @include media-max($mobileSmall) {
      font-size: 12px;
    }
  }

  &__actions {
    display: flex;
    gap: 20px;
    margin-top: 30px;

    opacity: 0;
    transform: translate3d(280px, 0, 0);
    transition: transform 0.5s ease 0.15s, opacity 0.5s ease 0.15s;
  }

  &__btn {
    display: inline-flex;
    align-items: center;
    min-width: 180px;

    &--more {
      svg {
        width: 7px;
        height: 12px;
      }
    }
  }

  :global(.swiper-slide-visible) & {
    &__image,
    &__title,
    &__text,
    &__info,
    &__actions {
      transform: translate3d(0, 0, 0);
      opacity: 1;
    }
  }
}
