
            @use 'sass:math';
            @use "src/styles/variables" as *;
            @use "src/styles/mixins" as *;
          
.new-episodes {
  &__title {
    margin-bottom: 30px;
  }

  &__subtitle {
    font-size: 24px;
    font-weight: 700;
    color: var(--main-color);
    margin-bottom: 20px;
  }

  &__body {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    &:not(:last-child) {
      margin-bottom: 25px;
    }
  }

  &__item {
  }

  &__btn-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  &__btn {
    svg {
      width: 7px;
      height: 13px;
      margin-bottom: 1px;
      margin-left: 1px;
    }
  }
}

