.new-episode {
  &__inner {
    display: flex;
    overflow: hidden;
    padding: 8px;
  }

  &__image {
    flex-shrink: 0;
    width: 106px;
    height: 150px;
    border-radius: 6px;
    margin-right: 20px;

    @media (any-hover: hover) {
      img {
        transition: transform 0.3s ease 0s;
      }
      &:hover img {
        transform: scale(1.035);
      }
    }
  }

  &__body {
    align-self: center;
    // padding: 10px 20px;
  }

  &__title {
    margin-bottom: 10px;

    a {
      line-height: math.div(20, 16);
      text-decoration: underline;
      text-decoration-color: transparent;
      transition: color 0.3s ease 0s, text-decoration-color 0.3s ease 0s;
    }

    &:hover a {
      color: var(--primary-color-dark);
      text-decoration-color: var(--primary-color-dark);
    }
  }

  &__list {
  }

  &__list-item {
    & + & {
      margin-top: 7px;
    }
  }

  &__list-item._broadcast .info-row__name {
    color: var(--accent-color);
    opacity: 1;
  }

  &__list-name {
  }

  &__list-values {
  }

  &__list-value {
  }
}
