
            @use 'sass:math';
            @use "src/styles/variables" as *;
            @use "src/styles/mixins" as *;
          
.footer {
  padding: 50px 0 40px;

  &__text {
    font-size: 14px;
    font-weight: 500;
    line-height: math.div(18, 14);
    text-align: center;
    & + & {
      margin-top: 5px;
    }
  }

  &__link {
    text-decoration: underline;
    &--primary {
      color: var(--primary-color);
    }
  }
}
